<template>
  <div id="app">
    <h1>Cashflow Ãœbersicht</h1>

    <form @submit.prevent="submitTransaction">
      <label for="betrag">Betrag:</label>
      <input type="number" v-model="newTransaction.amount" required />

      <label for="category">Kategorie:</label>
      <select v-model="newTransaction.category" required>
        <option value="Lebensmittel">Lebensmittel</option>
        <option value="Kleidung">Kleidung</option>
        <option value="Fahrtkosten">Fahrtkosten</option>
        <option value="Miete">Miete</option>
        <option value="Freizeit">Freizeit</option>
        <option value="Gesundheit">Gesundheit</option>
        <option value="Sonstiges">Sonstiges</option>
      </select>

      <label for="date">Datum:</label>
      <input type="date" v-model="newTransaction.date" required />

      <label for="note">Notiz:</label>
      <textarea v-model="newTransaction.note"></textarea>

      <button type="submit">HinzufÃ¼gen</button>
    </form>

    <h2>AusgabenÃ¼bersicht</h2>
    <ul>
      <li v-for="expense in expenses" :key="expense.id">
        ðŸ’° {{ expense.betrag }} â‚¬ â€“ {{ expense.verwendungszweck }} am {{ expense.datum }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      newTransaction: {
        amount: '',
        category: 'Lebensmittel',
        date: '',
        note: ''
      },
      expenses: []
    };
  },
  mounted() {
    this.fetchExpenses();
  },
  methods: {
    fetchExpenses() {
      fetch('https://cashflow-6.onrender.com/auszahlungen') 
          .then(response => response.json())
          .then(data => {
            this.expenses = data;
          })
          .catch(error => console.error('Fehler beim Laden der Ausgaben:', error));
    },
    submitTransaction() {
      fetch('https://cashflow-6.onrender.com/auszahlungen', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          betrag: this.newTransaction.amount,
          datum: this.newTransaction.date,
          zahlungsart: "KARTE",  // Beispielwert, dieser kann dynamisch sein
          verwendungszweck: this.newTransaction.category,
          notiz: this.newTransaction.note
        })
      })
          .then(response => response.json())
          .then(data => {
            this.fetchExpenses();
            this.newTransaction = { amount: '', category: 'Lebensmittel', date: '', note: '' };
          })
          .catch(error => console.error('Fehler beim HinzufÃ¼gen der Transaktion:', error));
    }
  }
};
</script>

<style scoped>
form {
  display: flex;
  flex-direction: column;
  width: 300px;
  margin: 0 auto;
}
input, select, textarea {
  margin: 8px 0;
  padding: 10px;
  font-size: 1rem;
}
button {
  padding: 10px;
  background-color: #42b983;
  color: white;
  border: none;
  cursor: pointer;
}
</style>
